.wrapper-petlayout {
    padding: 10px;
}
.grid-contents {
    display: grid;
    grid-template-columns: 2fr 1fr;
    text-align: center;
    padding: 10px;
    
}
.pet-info-section {
    display: grid;
    grid-template: "typ name" auto
                           "cst act" auto
                           "sze ene" auto;
    text-align: center;
    gap: 10px;
}
.type {
    grid-area: typ;
}
.name {
    grid-area: name;
}
.cost {
    grid-area: cst;
}
.activity {
    grid-area: act;
}
.size {
    grid-area: sze;
}
.energy {
    grid-area: ene;
}
.cost-section {
    display: grid;
    gap: 10px;
    padding: 10px;
}
.exp-time-section {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    padding: 10px;
}
.wrapper-petlayout div {
    min-height: 100px;
}
.image-pet-source {
    border-radius: 25% 10%;
}
div img {
    height: 500px;
}
